import React,{Component} from 'react';
class Session36 extends Component{
constructor(){
    super();
    this.state = {
        name: "",
        email: "",
        phone: "",
        course: "",
    }
}

changeName(e){
    this.setState({
        name:e,
    })
}

changeEmail(e){
    this.setState({
        email:e,
    })
}

changePhone(e){
    this.setState({
        phone:e,
    })
}

changeCourse(e){
    this.setState({
        course:e,
    })
}

    render(){
        return(

            <>
            <h1>React Forms</h1>
            <form>
                <label>Name:</label>
                <input value={this.state.name} 
                onChange={(e)=>this.changeName(e.target.value)}
     
     type="text"/><br/><br/>
                <label>Email:</label>
                <input value={this.state.email}
                onChange={(e)=>this.changeEmail(e.target.value)}
                 type="email"/><br/><br/>
                <label>Phone:</label>
                <input value={this.state.phone}
                onChange={(e)=>this.changePhone(e.target.value)}
                 type="number"/><br/><br/>

                 <label>Course:</label>
                 <select value={this.state.course}
                 onChange={(e)=>this.changeCourse(e.target.value)}
                 >
                <option value="Web Development">Web Development</option>
                <option value="Python">Python</option>
                <option value="Java">Java</option>
                </select>

            </form>
            <h2>User Details</h2>
            <h3>Name:{this.state.name}</h3>
            <h3>Email:{this.state.email}</h3>
            <h3>Phone:{this.state.phone}</h3>
            <h3>Course:{this.state.course}</h3>
            </>
        )
    }
}
export default Session36