
import React,{Component} from 'react';
class Session39 extends Component{

    constructor(){
        super();
        this.state = {
            error:null,
            isLoaded:false,
            data:[],
            
        }
    }


    componentDidMount(){
        const axios = require ('axios');
        axios.get("https://icanhazdadjoke.com/slack")
        .then((response)=>{
            this.setState({
                isLoaded:true,
                data:response.data.attachments[0].text,
            })

        })

        .catch((e)=>{
            this.setState({
                isLoaded: true,
                error:e,
            })
        })

        .then(()=>{
            console.log("i dont care i will run")
        })

    }

    render(){
        if(this.state.error){
            return(
                <>
                <h1>AXIOS</h1>
                <h3 class="joke">{this.state.data}</h3>
                </>
            )}

             else if(!this.state.isLoaded){
                return(<>
                    <h1>AXIOS</h1>
                    <h3>Loading...</h3>
                    </>
                )
             }




            



   else{
        return(
            <>
            <h1>AXIOS</h1>
            <h3 class="joke">{this.state.data}</h3>
            </>
        )
    }
}}
export default Session39;