import React,{Component} from 'react';
class Session37 extends Component{

    constructor(){
        super();
        this.state = {
            error:null,
            isLoaded:false,
            data:[],
            
        }
    }

componentDidMount(){
    fetch("https://dog.ceo/api/breeds/image/random")
.then(response=>response.json())
.then((response)=>{
    this.setState({
        isLoaded:true,
        data: response.message,
    },(error)=>{
        this.setState({
            isLoaded: true,
            error:error

        })
    })
})
}


render(){
    if(this.state.error){
        
    
return(
    <>
    <h1>Fetch in React</h1>
    
    <h2>Error 404 Page Not Found</h2>
    </>
)
}
else if(!this.state.isLoaded){
    return(
        <>
        <h1>Fetch in React</h1>
        <h2>Loading...</h2>
        </>
    )
}

else{
    return(
        <>
        <h1>Fetch in React</h1>
        <img src={this.state.data}></img>
        </>
    )

}
}

}
export default Session37;